export default {
	data() {
		return {
			orderList:[]
		}
	},
	onLoad(options) {
	var status = options.status
	uni.request({
		url: this.apiServer+'/api/order/read',
		data:{
			status:status,
			user_id:uni.getStorageSync('USERID')
		},
		method: 'GET',
		success: res => {
			if(res.data.err>0){
				uni.showModal({
					title:'信息提示',
					content:'没有订单信息',
					showCancel:false
				});
				setTimeout(function () {
				    uni.navigateBack({
				        delta: 1
				    });
				}, 2000);
			}else{
				this.orderList = res.data.data;
				console.log(res.data.data);
			}

		},
		fail: () => {},
		complete: () => {}
	});	
	},
	methods: {
		
	}
}
